import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import './MembersTable.css';

const MembersTable = () => {
    const [members, setMembers] = useState([]);
    const navigate = useNavigate();

    // Charger la liste des membres
    useEffect(() => {
        fetch("https://mlm-app.onrender.com/api/members")
            .then(res => res.json())
            .then(data => {
                // Vérification et génération d'un ID si nécessaire
                const updatedMembers = data.map(member => ({
                    ...member,
                    memberId: member.memberId || `RMR-${Math.floor(10000 + Math.random() * 90000)}`
                }));
                setMembers(updatedMembers);
            })
            .catch(err => console.error("❌ Erreur chargement des membres :", err));
    }, []);

    // Fonction pour modifier un membre
    const handleEdit = (member) => {
        localStorage.setItem("selectedMember", JSON.stringify(member));
        navigate('/members-form');
    };

    // Fonction pour supprimer un membre
    const handleDelete = async (memberId) => {
        if (window.confirm("❌ Êtes-vous sûr de vouloir supprimer ce membre ?")) {
            try {
                const response = await fetch(`https://mlm-app.onrender.com/api/members/${memberId}`, {
                    method: 'DELETE'
                });

                if (!response.ok) {
                    throw new Error("Erreur lors de la suppression du membre.");
                }

                alert("✅ Membre supprimé avec succès !");
                setMembers(members.filter(member => member._id !== memberId));
            } catch (error) {
                console.error("❌ Erreur suppression :", error);
                alert("❌ Impossible de supprimer ce membre.");
            }
        }
    };

    // Fonction pour afficher les détails du membre
    const handleShowMemberDetails = (member) => {
        const detailsWindow = window.open("", "_blank", "width=500,height=650");
        if (detailsWindow) {
            detailsWindow.document.write(`
                <html>
                <head>
                    <title>👤 Détails du Membre</title>
                    <style>
                        body { font-family: Arial, sans-serif; background-color: #f9f9f9; padding: 20px; text-align: center; }
                        .card { background: white; padding: 20px; border-radius: 10px; box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1); text-align: left; width: 90%; margin: auto; }
                        .card h3 { text-align: center; color: #007bff; }
                        .info { margin-bottom: 10px; font-size: 16px; }
                        .close-btn { display: block; width: 100%; margin-top: 15px; background-color: #dc3545; color: white; padding: 10px; border: none; cursor: pointer; border-radius: 5px; text-align: center; }
                        .close-btn:hover { background-color: #c82333; }
                    </style>
                </head>
                <body>
                    <div class="card">
                        <h3>👤 Détails du Membre</h3>
                        <p class="info"><strong>ID Membre :</strong> ${member.memberId}</p>
                        <p class="info"><strong>Prénom :</strong> ${member.firstName}</p>
                        <p class="info"><strong>Nom :</strong> ${member.name}</p>
                        <p class="info"><strong>Email :</strong> ${member.email}</p>
                        <p class="info"><strong>Téléphone :</strong> ${member.phone}</p>
                        <p class="info"><strong>Adresse :</strong> ${member.address || "Non renseignée"}</p>
                        <p class="info"><strong>Pays :</strong> ${member.country || "Non renseigné"}</p>
                        <p class="info"><strong>Ville :</strong> ${member.city || "Non renseignée"}</p>
                        <p class="info"><strong>Date d'inscription :</strong> ${member.registrationDate || "Non spécifiée"}</p>
                        <p class="info"><strong>Sponsor :</strong> ${member.sponsorId || "Aucun"}</p>
                        <button class="close-btn" onclick="window.close()">❌ Fermer</button>
                    </div>
                </body>
                </html>
            `);
        }
    };

    // Fonction pour afficher les produits souscrits
    const handleShowProducts = (member) => {
        const productsWindow = window.open("", "_blank", "width=600,height=500");
        if (productsWindow) {
            let productRows = member.products && member.products.length > 0
                ? member.products.map(product => `
                    <tr>
                        <td>${product.name}</td>
                        <td>${product.price} €</td>
                        <td>${product.subscriptionDate || "Non spécifiée"}</td>
                    </tr>
                `).join("")
                : "<tr><td colspan='3'>Aucun produit souscrit.</td></tr>";

            productsWindow.document.write(`
                <html>
                <head>
                    <title>📦 Produits souscrits</title>
                    <style>
                        body { font-family: Arial, sans-serif; background-color: #f9f9f9; padding: 20px; text-align: center; }
                        .table-container { background: white; padding: 20px; border-radius: 10px; box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1); width: 90%; margin: auto; }
                        table { width: 100%; border-collapse: collapse; margin-top: 10px; }
                        th, td { border: 1px solid #ddd; padding: 10px; text-align: left; }
                        th { background-color: #007bff; color: white; }
                        .close-btn { display: block; width: 100%; margin-top: 15px; background-color: #dc3545; color: white; padding: 10px; border: none; cursor: pointer; border-radius: 5px; text-align: center; }
                        .close-btn:hover { background-color: #c82333; }
                    </style>
                </head>
                <body>
                    <div class="table-container">
                        <h3>📦 Produits souscrits</h3>
                        <table>
                            <thead>
                                <tr>
                                    <th>Nom du Produit</th>
                                    <th>Prix</th>
                                    <th>Date de souscription</th>
                                </tr>
                            </thead>
                            <tbody>
                                ${productRows}
                            </tbody>
                        </table>
                        <button class="close-btn" onclick="window.close()">❌ Fermer</button>
                    </div>
                </body>
                </html>
            `);
        }
    };

    // Fonction pour afficher les Wallets du membre dans une nouvelle fenêtre
const handleShowWallets = async (member) => {
    try {
        const response = await fetch(`https://mlm-app.onrender.com/api/wallets/member/${member._id}`);
        const wallets = await response.json();

        const walletsWindow = window.open("", "_blank", "width=600,height=500");

        if (walletsWindow) {
            let walletRows = wallets.length > 0
                ? wallets.map(wallet => `
                    <tr>
                        <td>${wallet.walletName}</td>
                        <td>${wallet.publicAddress}</td>
                    </tr>
                `).join("")
                : "<tr><td colspan='2'>Aucun wallet enregistré.</td></tr>";

            walletsWindow.document.write(`
                <html>
                <head>
                    <title>💳 Wallets du Membre</title>
                    <style>
                        body { font-family: Arial, sans-serif; background-color: #f9f9f9; padding: 20px; text-align: center; }
                        .table-container { background: white; padding: 20px; border-radius: 10px; box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1); width: 90%; margin: auto; }
                        table { width: 100%; border-collapse: collapse; margin-top: 10px; }
                        th, td { border: 1px solid #ddd; padding: 10px; text-align: left; }
                        th { background-color: #007bff; color: white; }
                        .close-btn { display: block; width: 100%; margin-top: 15px; background-color: #dc3545; color: white; padding: 10px; border: none; cursor: pointer; border-radius: 5px; text-align: center; }
                        .close-btn:hover { background-color: #c82333; }
                    </style>
                </head>
                <body>
                    <div class="table-container">
                        <h3>💳 Wallets du Membre</h3>
                        <table>
                            <thead>
                                <tr>
                                    <th>Nom du Wallet</th>
                                    <th>Adresse Publique</th>
                                </tr>
                            </thead>
                            <tbody>
                                ${walletRows}
                            </tbody>
                        </table>
                        <button class="close-btn" onclick="window.close()">❌ Fermer</button>
                    </div>
                </body>
                </html>
            `);
        }
    } catch (error) {
        console.error("❌ Erreur lors du chargement des wallets :", error);
        alert("❌ Impossible de récupérer les wallets du membre.");
    }
};

    return (
        <div className="table-container">
            <h2>📋 Liste des Membres</h2>
            <table>
                <thead>
                    <tr>
                        <th>ID Membre</th>
                        <th>Prénom</th>
                        <th>Nom</th>
                        <th>Email</th>
                        <th>Téléphone</th>
                        <th>Actions</th>
                    </tr>
                </thead>
                <tbody>
                    {members.map(member => (
                        <tr key={member._id}>
                            <td>{member.memberId}</td>
                            <td>{member.firstName}</td>
                            <td>{member.name}</td>
                            <td>{member.email}</td>
                            <td>{member.phone}</td>
                            <td className="action-buttons">
                                <button className="view-btn" onClick={() => handleShowProducts(member)}>📦 Voir Produits</button>
                                <button className="edit-btn" onClick={() => handleEdit(member)}>✏️ Modifier</button>
                                <button className="details-btn" onClick={() => handleShowMemberDetails(member)}>👁️ Voir Détails</button>
                                <button className="delete-btn" onClick={() => handleDelete(member._id)}>🗑️ Supprimer</button>
                                <button className="wallet-btn" onClick={() => handleShowWallets(member)}>💳 Voir Wallets</button>
                            </td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    );
};

export default MembersTable;